.footer {
  background-color: #374151;
  padding: 10px 0;
}

.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer-nav {
  margin-right: 20px;
}

.footer-links {
  list-style: none;
  display: flex;
}

.footer-links li {
  margin-right: 10px;
}

.footer-links a {
  color: #fff;
  text-decoration: none;
  font-size: 14px;
}

.footer-info {
  color: #fff;
}

.footer-text {
  margin-bottom: 5px;
  font-size: 12px;
}
