/* header.css */
.header {
  background-color: #3b82f6;
  position: sticky;
  top: 0;
  z-index: 50;
}
.header .logo {
  color: white;
  font-size: 1.5rem;
  width: 70px;
  text-decoration: none;
}
.header .container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
}

.header ul {
  list-style: none;
  display: flex;
  gap: 1rem;
  color: white;
}

.header ul li {
  display: inline-block;
  margin-right: 1rem;
}

.header ul li a {
  font-size: 18px;
  color: white;
  text-decoration: none;
}

.header ul li a:hover {
  color: #ccc;
}

.sign-up-button {
  background-color: #4d9ffc;
  color: #333;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
}

.sign-up-button:hover {
  background-color: #fcd34d;
}
@media (max-width: 768px) {
  .container,
  .header {
    flex-direction: column;
  }

  .header {
    margin-top: 1rem;
  }

  .header li {
    margin-right: 0;
    margin-bottom: 0.5rem;
  }
}
