.activity-section {
  padding-top: 2rem;
}

.container {
  margin: 0 auto;
  max-width: 960px;
  padding: 0 1rem;
}

.section-title {
  font-size: 1.5rem;
  font-weight: bold;
  text-align: center;
  margin-bottom: 2rem;
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 2rem;
}

.card {
  height: 100%;
  background-color: #3182ce;
  padding: 1rem;
  border-radius: 0.5rem;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  transform-origin: center;
  transition: transform 0.3s;
}

.card:hover {
  transform: scale(1.05);
  cursor: pointer;
}

.activity-image {
  height: 50%;
  display: block;
  margin: 0 auto;
  margin-bottom: 1rem;
}

.activity-title {
  font-size: 1.125rem;
  font-weight: bold;
  color: #fff;
  margin-bottom: 0.5rem;
}

.activity-description {
  color: #edf2f7;
}
