/* Contact.css */

.section-title {
  text-align: center;
  font-size: 24px;
  margin-bottom: 20px;
}

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.contact__container {
  background-color: #3b82f6;
  border-radius: 8px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
}

.contact__options {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 30px;
}

.contact__option {
  flex-basis: 200px;
  text-align: center;
  margin: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contact_option-icon {
  font-size: 36px;
  margin-bottom: 10px;
}

.contact__option h4 {
  font-size: 18px;
  margin-bottom: 5px;
}

.contact__option h5 {
  font-size: 14px;
  color: #ffffff;
  margin-bottom: 10px;
}

.contact__option a {
  display: block;
  text-decoration: none;
  color: #ffffff;
  margin-top: 10px;
}

.contact__option a:hover {
  color: #ffffff;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

input[type="text"],
input[type="email"],
textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #000000;
  border-radius: 4px;
  resize: vertical;
}

textarea {
  height: 120px;
}

.btn {
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.btn-primary {
  background-color: #ffffff;
}

.btn-primary:hover {
  background-color: #000000;
  color: #ffffff;
}

.btn:focus {
  outline: none;
}

/*==============MEDIA QUERIES (MEDIUM DEVICES)=======*/
@media screen and (max-width: 1024px) {
  .container.contact__container {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
}

/*==============MEDIA QUERIES (SMALL DEVICES)=======*/
@media screen and (max-width: 600px) {
  .container.contact__container {
    width: var(--container-width-sm);
  }
}
