/* Instructor Heading */
.instructor-heading {
  padding-top: 4rem;
  color: #2196f3;
  font-weight: bold;
  font-size: 2rem;
  text-align: center;
}

/* Instructor Subheading */
.instructor-subheading {
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
}

/* Instructor Section */
.instructor-section {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
}

/* Instructor Grid */
.instructor-grid {
  display: flex;
  justify-content: center;
  margin-top: 8px;
  margin-bottom: 4px;
}

/* Instructor Card */
.instructor-card {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 12px;
  width: 25%;
  transition: transform 0.3s, box-shadow 0.3s;
}

.instructor-card:hover {
  transform: scale(1.1);
  box-shadow: 0 8px 12px rgba(0, 0, 0, 0.2);
}

/* Instructor Image Box */
.instructor-img-box {
  position: relative;
}

/* Instructor Image */
.instructor-img {
  width: 256px;
  height: 256px;
  border-radius: 8px;
}

/* Social Link */
.social-link {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
}

/* Social Link Icons */
.social-link a {
  color: #2196f3;
  margin: 4px;
  font-size: 1.5rem;
}

/* Instructor Name */
.instructor-name {
  font-size: 1.5rem;
}

/* Instructor Title */
.instructor-title {
  font-size: 1.2rem;
}
